import React, {useState} from 'react';
import './endscreen.css';

const Endscreen = React.memo(({handleClick}) => {

	const [state, setState] = useState('endscreen__entering');

	setTimeout(() => {
		if( state === 'endscreen__entering') setState('endscreen');
	},200);

	return (
		<div className={`${state}`}>
				<div className="endscreen__text">
					<h3 className="endscreen__title">Terima kasih para MT yang telah memilih staff favoritnya</h3>
					<h4 className="endscreen__description">Nantikan hasilnya di media sosial BA Pontianak</h4>
				</div>
				{/*<button className="endscreen__start-button" onClick={(e) => endButtonClicked(e)}>Kembali</button>*/}
					
		</div>
	  )
});

export default Endscreen;